.app_wrapper {
  max-width: 1240px;
  min-height: 100vh;
  margin: 0 auto;
  display: grid;
  grid-template-areas:
    "header header header"
    "aside main main";
  grid-template-rows: 60px 1fr;
  grid-template-columns: 2fr 10fr;
}

main {
  grid-area: main;
  background-color: greenyellow;
}
